body {
  background-image: url('background.webp');
  background-size: cover;

}

div {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center
}

table {
  border-collapse: collapse;
  margin: 5px;
}

th,
td {
  background-color: white;
  border: 5px solid black;
  width: fit-content;
  font-size: xx-large;
  font-weight: bolder;
}

input,
button {
  width: fit-content;
}


#gameDiv,
#homeDiv,
#scoreDiv {
  display: flex;
  background-image: url('cards.jpeg');
  background-size: contain;
  border: 5px solid black;
  height: fit-content;
  width: 700px;
  margin: 20%;
  text-align: center;
  font-size: xx-large;
}

#gameDisplay {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

button,
input {
  text-align: center;
  font-size: xx-large;
  margin-bottom: 5px;
  background: none;
  border: 5px solid black;
}

p {
  color: blue;
  margin: 0%
}

h6 {
  margin: 0%;
  size: 0cap;
}

#playerDiv {
  margin-top: 50px;
}

#comDiv {
  margin-bottom: 50px;
}

h1 {
  font-size: 50px;
  position: inherit;
  animation: bounce 5s infinite;
  margin-bottom: 0px;
}

@keyframes bounce {
  0%, 100% {
    transform: scale(1.5);
    color: red;
  }
  50% {
    transform: translateY(-50px);
    color: blue;
  }
}

 .cardDiv {
  position: relative;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  height: 200px;
  width: 150px;
}

.cardNumber {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 50px;
  font-weight: bold;
  text-align: center;
}

.smallNumbers {
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
}

.smallNumber {
  font-size: 30px;
  font-weight: bold;
  text-align: center;
  width: 30%;
}

.smallNumber.top-left {
  position: absolute;
  top: 5px;
  left: 5px;
}

.smallNumber.top-right {
  position: absolute;
  top: 5px;
  right: 5px;
}

.smallNumber.bottom-left {
  position: absolute;
  bottom: 5px;
  left: 5px;
}

.smallNumber.bottom-right {
  position: absolute;
  bottom: 5px;
  right: 5px;
}

.Club{
  font-size: xx-large;
}


 








